import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-boxchat',
  templateUrl: './boxchat.component.html',
  styleUrls: ['./boxchat.component.scss']
})
export class BoxchatComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    // tslint:disable-next-line:only-arrow-functions
    $(document).ready(function() {
      let chatHeight = $('.height-box').height();
      $('#stylescrollbar').animate({ scrollTop : chatHeight});
});
}
}
